.list-of-genres {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  width: 100%;
  /* columns: 5; */
  /* position: absolute;
  left: 5%;
  right: 5%;
  width: 80%; */
  /* margin: 0 auto; */
  
  margin-bottom: 2rem;
}

.list-of-genres .btn-container {
  /* position: relative; */
  width: 210px;
  height: .9em;
  border-bottom: 4px dotted #057da1;
  /* border-bottom: 4px dotted #035c77; */
  margin-right: 20px;
  margin-bottom: 2px;
  overflow: hidden;
  /* text-align: center; */
  transition: all .5s cubic-bezier(0.5, 1, 0.92, 1);
}
.list-of-genres:hover .btn-container {
  height: 1.2em;
}
.list-of-genres .reset-btn {
  /* width: calc(100% / 4); */
  /* display: block; */
  width: 210px;
  padding: 0;
  border: none;
  margin-right: 20px;
  /* margin-top: -20px; */
  color: rgb(255, 255, 255);
  font-size: 1.2rem;
  font-weight: 300;
  line-height: 1.25rem; 
  letter-spacing: 1px;
  text-align: left;
  transition: all .3s ease;
  /* font-weight: 500; */
  /* letter-spacing: -.5px; */
  /* word-spacing: -2px; */
}
