.info-tab {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  background-color: #04171db7;
  z-index: 1000;
}

.info-tab>div {
  width: 80%;
  margin: 0 auto;
  /* position: relative; */
}

.info-tab>div>div:not(.hover-zone){
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  max-width: 925px;
  height: 70vh;
  /* width: 100%; */
  /* min-height: 60vh; */
  overflow-y: auto;
  /* max-height: 80vh; */
  padding: 3rem;
  border: 2px solid #035c77;
  border-radius: 3.5px;
  margin: 18vh auto;
  background-color: #051c24;
  box-shadow: inset 0 0 80px 40px rgb(8, 11, 24);
  text-align: left;
  text-align: justify;
  position: relative;
  /* color: rgb(114, 114, 114); */
}

.info-tab .text-cont {
  flex: 66.6%;
  width: 100%;
  max-width: 70vw;
  padding: 2rem 3rem 2rem 0;
}

/* .info-tab .text-cont p {
  height: 100%;
  margin: auto;
} */

/* .info-tab .img-cont {
  width: 100%;
} */

.info-tab .genres-val {
  margin-bottom: .7rem;
  text-transform: lowercase;
  /* border-bottom: 2px dashed #057da1; */
  font-family: 'Poiret One', cursive;
  font-size: 1.25rem;
  color: rgb(255, 255, 255);
}
.info-tab h3 {
  margin-top: .3rem;
  margin-bottom: 1.4rem;
  font-family: 'Poiret One', cursive;
  font-size: 1rem;
}
.info-tab .vote-val {
  font-size: 1.6rem;
  color: rgb(255, 255, 255);
}
.info-tab .lang-val {
  display: inline-block;
  min-width: 37px;
  padding: 5px 5px 3px;
  border: 1px solid #035c77;
  border-radius: 3.5px;
  /* border-radius: 50px; */
  /* margin-top: calc(2rem - 1em); */
  margin-right: .7rem;
  margin-bottom: 2.1rem;
  text-align: center;
  text-transform: lowercase;
  /* font-family: 'Poiret One', cursive; */
  font-size: 1.1rem;
  /* font-weight: 600; */
  line-height: 30px;
  color: #fff;
  /* vertical-align: middle; */
}

.btns-container {
  position: relative;
  top: 4.3em;
  width: 92px;
  /* height: 2.1rem; */
  /* height: calc(2rem - 1em); */
  /* margin-bottom: 2.1rem; */
  /* margin-top: calc(2rem - 1em); */
}
.btns-container>*:first-child, .btns-container>*:nth-child(2) {
  padding: 5px;
  border: 1px solid #035c77;
  border-radius: 3.5px;
  /* margin-top: calc(2rem - 1em); */
}

.info-tab .img-cont {
  flex: 33.4%;
  /* width: 100%; */
  /* max-width: 33vw; */
  min-width: 230px;
  align-self: center;
  text-align: center;
}
.info-tab .img-cont img {
  width: 100%;
  max-width: 33vw;
  /* height: 100%; */
  object-fit: cover;
}

.img-cont button, .del-acc-tab .del-acc-btn {
  display: block;
  padding: 20px;
  margin: 10px auto;
}

.hov-btn:hover {
  background-color: rgb(255, 226, 59);
  color: #04171d;
  font-weight: 700;
}

.profile-cont p {
  padding: 40px 0 8px 0;
}

input[type='checkbox'] {
  margin: 0 12px;
  cursor: pointer;
}

.info-tab .del-acc-tab {
  position: fixed;
  top: calc(50% - 150px);
  left: calc(50% - 200px);
}
.info-tab .del-acc-tab>div {
  /* display: block !important; */
  margin: 0 !important;
  width: 400px;
  height: 300px !important;
}