@font-face {
  font-family: 'Showtime Normal';
  font-style: normal;
  font-weight: normal;
  src: local('Showtime Normal'), url('/Showtime.woff') format('woff');
}

@import url('https://fonts.googleapis.com/css2?family=Josefin+Sans&family=Poiret+One&display=swap');

button {
  background: none;
  border: 1px solid #035c77;
  border-radius: 50px;
  color: rgb(255, 226, 59);
  /* font-family: 'Poiret One', cursive; */
  font-family: 'Josefin Sans', sans-serif;
  line-height: 0;
  /* font-weight: 900; */
  /* letter-spacing: -1.4px; */
  cursor: pointer;
  transition: all .3s ease;
}

a {
  color: rgb(255, 226, 59);
}

ul {
  padding: 0;
  margin: 0;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

/* @media (prefers-reduced-motion: no-preference) {
