.App-header {
  width: 100%;
  /* background-color: #000c24af; */
  min-height: 10vh;
  /* max-height: 40vh; */
  /* display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center; */
  background-image: linear-gradient(#000c24 5%, #000c247e 100%);
  background-image: linear-gradient(#000000ec 5%,  #04171dab 90%);
  /* border-bottom: 2px dashed #0d889e; */
  /* text-align: left; */
  /* font-size: calc(10px + 2vmin); */
  font-size: 1.5rem;
  font-family: 'Poiret One', cursive;
  font-weight: bold;
  /* color: white; */
  position: fixed;
  /* overflow-y: hidden; */
  z-index: 100;
}

.App-header nav {
  width: 80%;
  margin: 0 auto;
}

.App-header .logo-motto, .App-header .first-nav {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
}

/* .App-header .first-nav, .App-header .second-nav {
  width: 80%;
  margin: 0 auto;
} */

.App-header .first-nav>* {
  padding: 2rem 0;
}
.App-header .motto {
  padding-left: 2rem;
  padding-right: 2rem;
  /* color: #057da1; */
  /* color: rgba(255, 226, 59); */
  line-height: 2rem;
  font-size: 1.5rem;
  
}

.App-header .logo {
  height: 10vh;
  border: none;
  line-height: normal;
}

.App-header .logo h1 {
  /* color: rgb(255, 226, 59); */
  /* line-height: .7rem; */
  /* font-size: 48px; */
  font-size: 3rem;
  font-family: 'Showtime Normal', sans-serif;
  letter-spacing: -1px;
  white-space: nowrap;
  /* color: rgb(248, 245, 225); */
}
.App-header .logo h2 {
  padding-left: 5px;
  color: #fff;
  /* color: #057da1; */
  font-weight: 600;
  line-height: .7;
  font-size: 16px;
  letter-spacing: 2.5px;
  white-space: nowrap;
  /* word-spacing: 3px; */
}

.App-header .search-random {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.App-header .search-random>* {
  margin-left: .5rem;
}

.App-header .second-nav {
  margin-right: -20px;
}
/* .second-nav {
  text-align: left;
} */
/* this display:inline-block is not pretty correct!!!!! */
.App-header .second-nav li {
  display: inline-block;
}

.App main {
  min-height: 100vh;
  padding-top: 300px;
  background-color: #04171d;
  /* color: rgb(255, 226, 59); */
}

