.wanted-btn, .seen-btn {
  position: absolute;
  bottom: 2px;
  transform: scale(-1, 1);
  /* z-index: 1000; */
  /* background-color: #04171db4; */
  background-color: #04171ded;
}
.seen-btn {
  right: 0;
}

.wanted-btn>button, .seen-btn>button {
  display: block;
  /* width: 50px; */
  padding: .1rem;
  border: none;
  border-radius: 0;
  /* background-color: #04171db4; */
  font-size: 1.4rem;
  opacity: .7;
  transition: all .3s ease;
}

.wanted-btn>button:hover, .seen-btn>button:hover {
  opacity: 1;
}

.checked-btn {
  /* color: #035c77; */
  color: #2eb0d7;
}