.movie-card {
  position: relative;
  
  border: 1px solid #035c77;
  border-radius: 3px;
  /* width: 210px;
  height: 360px; */
  margin: 10px;
  background-color: #04171d;
  /* color: rgb(255, 226, 59); */
  /* overflow: hidden; */
  cursor: pointer;
  /* box-shadow: inset 10px 10px 15px 5px #04171d; */
  /* background: radial-gradient(circle, rgb(255, 226, 59) 60%, #04171d 95%); */
  /* mix-blend-mode: difference; */
  transition: all .5s cubic-bezier(0.5, 1, 0.92, 1);
}

.movie-card:hover {
  color: #04171d;
  background-color: rgb(255, 226, 59); 
}
.movie-card>div:first-child {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 210px;
  height: 360px;
  overflow: hidden;
}
.movie-card:hover img {
  transform: scale(1.04);
}
.movie-card:hover h2 {
  font-weight: 600;
}

.movie-card .img-container {
  /* width: 100%; */
  height: 80%;
  background-color: #04171d;
}
.movie-card img {
  height: 100%;
  width: 100%;
  object-fit: cover;
  transform: scale(1);
  transition: transform .35s cubic-bezier(0.9, 1, 0.92, 1);

}
.movie-card .text-container {
  /* height: fit-content; */
  height: 20%;
  padding: 0.65rem 0.25rem;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.movie-card h2 {
  max-height: 1.85rem;
  /* margin: 0.6rem 0.25rem; */
  /* border-top: 3.5px dashed rgb(255, 255, 255); */
  /* margin: 0; */
  margin-bottom: 0.6rem;
  margin: auto;
  font-size: 0.95rem;
  font-weight: 400;
  letter-spacing: .015rem;
  word-spacing: -.15rem;
  overflow: hidden;
}

.movie-card h3 {
  padding: 0 0.3rem;
  margin: 0;
  font-size: 0.8rem;
  font-weight: 200;
  /* border-bottom: 3.5px dashed rgb(255, 255, 255); */
}