#acc-bar {
  position: fixed;
  left: -100vw;
  bottom: 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
  /* width: 120%; */
  /* height: 10vh; */
  /* background-color: rgba(0, 255, 255, 0.192); */
  background-image: linear-gradient(#04171d91 10%, #031319 90%);
  /* transition: all 2s ease; */
  transition: all .75s cubic-bezier(0.5, 1, 0.92, 1);
  /* pointer-events: none; */
}


#acc-bar>button {
  display: block;
  width: 15vw;
  /* height: 3rem; */
  border: none;
  margin: 0 .8rem;
  line-height: normal;
  /* pointer-events: auto; */
}
#acc-bar>button>div:first-child {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  /* width: 10rem; */
  /* height: 4rem; */
  /* border: 1px solid #035c77;
  border-radius: 50px; */
  /* padding: 1rem 0; */
  /* height: 55px; */
  font-size: 1.5rem;
  /* text-align: center; */
  /* pointer-events: auto; */
}
#acc-bar>button>div:first-child>span {
  width: 4rem;
  height: 4rem;
  padding: 1rem; 
  border: 1px solid #035c77;
  border-radius: 50px;
  margin-right: 2.7vw;
  font-size: 2rem;
  transition: all .75s cubic-bezier(0.5, 1, 0.92, 1);
}
#acc-bar>button>div:first-child>span:hover {
  background-color: rgb(255, 226, 59);
  color: #04171d;
}
#acc-bar>button>div:nth-child(2) {
  /* white-space: normal; */
  /* margin-top: 0.8rem;
  margin-left: 5vw; */
  margin: 0.5rem 0 0.5rem 5vw;
}
#acc-bar.opened {
  left: 0vw;
}
.acc-lists {
  display: flex;
  justify-content: space-between;
  width: 95vw;
  /* background-image: linear-gradient(, ); */
  /* background-image: linear-gradient(#04171dab 10%, #04171d 100%); */
}
.edit-profile {
  border-color: transparent;
  transition: all .3s cubic-bezier(0.5, 1, 0.92, 1);
}
.edit-profile:hover {
  border-color: inherit;
}
.acc-lists>* {
  padding: .5rem;
  margin: 2rem;
  line-height: normal;
}

