.App-header .first-nav .random-btn {
  height: 10vh;
  padding: 0 1rem;
  border-radius: 3px;
  background-color: rgb(255, 226, 59);
  color: #04171d;
  font-size: 16px;
  line-height: 16px;
  letter-spacing: 2.5px;
  transition: all .6s ease;
}

.App-header .first-nav .random-btn:hover {  
  background-color: inherit;
  color: inherit;
}