.hover-zone {
  width: 2.5rem;
  height: 2.5rem;
  position: absolute;
  top: 20px;
  right: 20px;
  text-align: center;
  /* z-index: 100; */
  transition: all .3s ease;
}

/* .hover-zone:hover {
  top: -2.5rem;
  height: calc(50% + 5rem);
} */

.hover-zone button {
  /* display: inline; */
  width: 100%;
  height: 100%;
  padding: 5px;
  border: 1px solid #035c77;
  border-radius: 50px;
  /* background-color: rgb(255, 226, 59); */
  /* color: #035c77; */
  font-size: 1rem;
  /* font-weight: 500; */
  letter-spacing: 0;
  transition: all .3s ease;
  /* z-index: -1000; */
}

.hover-zone button:hover {
  background-color: rgb(255, 226, 59);
  /* border-color: #04171d; */
  color: #04171d;
}