.search-input {
  height: 44px;
  min-width: 310px;
  padding: .45rem;
  border: 1.5px solid #057da1;
  border-radius: 30px;
  outline: none;
  text-indent: 2.25%;
  font-family: 'Josefin Sans', sans-serif;
  /* font-family: 'Poiret One', cursive; */
  font-size: 18px;
  font-weight: 500;
  letter-spacing: -.5px;
  word-spacing: -2px;
  background-color: unset;
  color: #fff;
}